body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media all and (display-mode: standalone) {}

/* Generic styles */

@media only screen and (min-width: 1009px) {

  #root {
    background-image: url('./banner.webp');
    background-position: 0% 8rem;
    background-size: 120rem auto;
    background-repeat: no-repeat;
  }

  #root>div {
    padding-bottom: 8rem;
  }

  #main {
    background: #fff;
    margin-top: 3rem;
    padding: 3rem;
    box-shadow: 0 1.25rem 3rem .66rem rgb(20 61 80 / 15%);
    border-radius: 2rem;
  }

}

#footer {
  background-color: #287A9F;
}

/* TopNavBar */
.active::after {
  content: "";
  position: absolute;
  inset-block-end: 0;
  inset-inline-start: calc(0.75rem - 0.25rem);
  inset-inline-end: calc(0.75rem - 0.25rem);
  height: 0.125rem;
  background-color: #287A9F;
  display: block;
}

/* Brand styles */

.canvas .active::after,
.canvas [class$="topNavBarBrand__iconContainer"],
.canvas #footer {
  background-color: #E72429;
}

