body:not(.instructure) [class$="topNavBarBrand__iconContainer"] svg {
  visibility: hidden;
}

[class$="topNavBarBrand__iconContainer"] {
  background-size: 2.5rem !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
}

/* Instructure UI overrides */

li [data-cid="Checkbox"],
li [class$="checkbox"] {
  opacity: inherit;
}

pre {
  font-family: Menlo, Consolas, Monaco, "Andale Mono", monospace !important;
}

#toggleFilters {
  display: none;
}

/* markdown styles */

.markdown [class$="toggleDetails__details"] {
  padding-left: 0 !important;
}

.markdown [class$="toggleDetails__content"] {
  border: 1px solid rgb(199, 205, 209);
  border-radius: 1rem 0;
}

.markdown h2 {
  margin-top: 3rem !important;
}

/* Language styles */

#language .lang,
#language .lang:hover,
#language .lang:focus #language .lang:active {
  color: inherit;
  display: block;
  text-decoration: inherit;
}

#language [id$="__label"]:not(:has(a)),
#language [id$="__label"] a {
  padding: 0.5rem 1rem;
}

#language [role="menuitem"] {
  padding: 0;
}

#language,
[class$="view-drilldown__container"] {
  width: 10rem;
  min-width: 10rem !important;
}